@use "variables";
@use "mixins";

.subscribe {
    font-family: "Open Sans", sans-serif;

    text-transform: capitalize;

    &__container {
        margin-bottom: mixins.toRem(140);

        & > *:not(:last-child) {
            margin-bottom: mixins.toRem(35);
        }
    }

    &__title {
        font-family: "Roboto", sans-serif;
        font-size: mixins.toRem(50);
        font-weight: 500;

        text-align: center;
    }

    &__filter {
    }
}
